.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  padding-top: 10rem;
  padding-bottom: 8rem;
  & .modalBody {
    & h3 {
      margin-left: 25%;
    }
    position: relative;
    padding: 2rem;
    width: 22rem;
    border-radius: 5px;
    background: rgb(209, 198, 198);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: left;
    font-size: 1.3rem;
    & .close {
      position: absolute;
      top: 0px;
      right: 0px;
      width: 30px;
      height: 30px;
      border-radius: 5px;
      background: transparent;
      border: none;
      font-size: 17px;
      color: black;
      text-align: center;
      margin: 0;
      &:hover {
        background: red;
      }
    }
  }
}

input:hover {
  cursor: pointer;
}

input[type=range] {
  height: 38px;
  -webkit-appearance: none;
  margin: 5px 0;
  width: 100%;
  background: transparent;
}
input[type=range]:focus {
  outline: none;
}
input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 10px;
  cursor: pointer;
  //animate: 0.2s;
  background:linear-gradient(135deg, rgba(255,255,255,1) 0%, rgba(58,72,221,1) 100%);
  border-radius: 5px;
  border: 1px solid #000000;
}
input[type=range]::-webkit-slider-thumb {
  box-shadow: 1px 1px 1px #000000;
  border: 1px solid #000000;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: radial-gradient(at 20% 40%, rgb(255,255, 255) 2%, rgb(255,255, 255) 3%, rgba(10,20,10,1) 70%, rgba(10,20,10,1) 10%);

  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -6px;
}
input[type=range]:focus::-webkit-slider-runnable-track {
  background:linear-gradient(135deg, rgba(255,255,255,1) 0%, rgba(58,72,221,1) 100%);
  border: 1px solid #000000;
}

